import React, { useEffect, useState } from "react";
import { Row, Col, Select, Form, Input, Divider } from "antd";
import axios from "axios";
import moment from "moment";
import Send from "../../assets/icons/send.svg";
import Note from "./Note";
import Activity from "./Activity";
import Assigned from "./Assigned";
import authHeader from "../../services/auth-header";
import { BASE_URL } from "../../userconfig";
import * as AppStorage from "../../utils/AppStorage";
import { Scrollbars } from "react-custom-scrollbars";
import "./hotlineMessages.scss";
import { isMobile } from "mobile-device-detect";
import { STATUS } from "../../labels";
import {
  LAST_READ,
  STATUS_CHANGED,
  URGENCY_LEVEL_CHANGED,
  URGENCY_LEVEL,
  SEND_MESSAGE_TEXT,
} from "../../headings";
import {
  STATUS_CLOSED,
  STATUS_NEW,
  STATUS_OPEN,
  URGENCY_LEVEL_HIGH,
  URGENCY_LEVEL_LOW,
  URGENCY_LEVEL_MEDIUM,
} from "../../values";

var CryptoJS = require("crypto-js");
const { TextArea } = Input;
const { Option } = Select;
const obj_status = [
  { id: 1, name: STATUS_NEW },
  { id: 2, name: STATUS_OPEN },
  { id: 3, name: STATUS_CLOSED },
];

const obj_urgencyLevel = [
  { id: 1, name: URGENCY_LEVEL_LOW },
  { id: 2, name: URGENCY_LEVEL_MEDIUM },
  { id: 3, name: URGENCY_LEVEL_HIGH },
];

const Messages = ({
  conversation,
  urgencyLevel,
  status,
  onChangeUrgencyLevel,
  onChangeStatus,
  searchString,
  newState,
  setCounter,
  socket,
  chatdiv,
}) => {
  const [form] = Form.useForm();
  const [convoMessages, setConvoMessages] = useState([]);
  const [message, setMessage] = useState("");
  const [convMsg, setConvMsg] = useState({});
  const [displayNote, setDisplayNote] = useState({
    noteDivClicked: false,
    myClass: "",
  });
  const [displayActivity, setDisplayActivity] = useState({
    activityDivClicked: false,
    myClass: "",
  });

  const [displayAssigne, setDisplayAssigne] = useState({
    assigneDivClicked: false,
    myClass: "",
  });

  const [hideDiv, sethideDiv] = useState(false);
  const [searchBy, setSearchBy] = useState("");

  useEffect(() => {
    setSearchBy(searchString);
  }, [searchString, conversation.conversation_id]);

  useEffect(() => {
    getHotlineMessages();
    connect();
    var activity = document.getElementById("activityId");
    var note = document.getElementById("noteId");
    var assigne = document.getElementById("assigneId");
    if (displayAssigne.myClass != "") {
      setDisplayAssigne({ myClass: "", assigneDivClicked: false });
      assigne.classList.remove("selected");
    }
    if (displayNote.myClass != "") {
      setDisplayNote({ myClass: "", noteDivClicked: false });
      note.classList.remove("selected");
    }
    if (displayActivity.myClass != "") {
      setDisplayActivity({ myClass: "", activityDivClicked: false });
      activity.classList.remove("selected");
    }
  }, [conversation.conversation_id, newState]);

  useEffect(() => {
    if (convoMessages.length > 0) {
      if (searchBy.length > 1) {
        let count = 0;
        convoMessages.map((value) => {
          if (value.message.includes("</mark>")) {
            count += 1;
          }
          return count;
        });
        var highlightedMessage = document.getElementsByTagName("mark")[
          count - 1
        ];
        if (highlightedMessage) {
          highlightedMessage.scrollIntoView(false, { block: "end" });
        }
      } else {
        var lastMessage = document.getElementsByClassName("hotline_message")[
          document.getElementsByClassName("hotline_message").length - 1
        ];
        if (lastMessage) {
          lastMessage.scrollIntoView(false, { block: "end" });
        }
      }
    }
  }, [convoMessages, searchString]);

  useEffect(() => {
    const messagevalue =
      convMsg[conversation.conversation_id] !== undefined
        ? convMsg[conversation.conversation_id]
        : "";
    setMessage(messagevalue);
  }, [conversation.conversation_id]);

  const getHotlineMessages = async () => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    let convID = "";
    if (conversation.conversation_id.includes("<mark")) {
      convID = conversation.conversation_id
        ?.toString()
        .replace(/(<([^>]+)>)/gi, "");
    } else {
      convID = conversation.conversation_id;
    }
    var data = {
      conversation_id: convID,
      user_id: parseInt(
        CryptoJS.AES.decrypt(
          user.user_details.org_user_id,
          "anonymoose"
        ).toString(CryptoJS.enc.Utf8)
      ),
    };

    axios
      .post(BASE_URL + "hotlineConvoMessages/", data, options)
      .then(async (res) => {
        setConvoMessages(res.data);
      });
  };

  const handleChange = (e) => {
    setMessage(e.target.value);
    setConvMsg({ ...convMsg, [e.target.name]: e.target.value });
  };

  const keyDown = (e) => {
    if (
      (e.altKey && e.keyCode === 13) ||
      (e.shiftKey && e.keyCode === 13) ||
      (e.ctrlKey && e.keyCode === 13)
    ) {
      var textarea = document.querySelector(".input-control");
      textarea.value = textarea.value + "\r\n";
    }
    if (!e.altKey && !e.shiftKey && !e.ctrlKey && e.keyCode === 13) {
      e.preventDefault();
      onSendMessageClick();
    }
  };

  const onSendMessageClick = () => {
    const token = authHeader();
    let convID = "";
    if (conversation.conversation_id.includes("<mark")) {
      convID = conversation.conversation_id
        ?.toString()
        .replace(/(<([^>]+)>)/gi, "");
    } else {
      convID = conversation.conversation_id;
    }
    const user = JSON.parse(AppStorage.get("user"));
    if (message.length > 0) {
      const options = {
        headers: {
          Authorization: token,
          Accept: "application/json, text/plain",
          "Content-Type": "application/json;charset=UTF-8",
        },
      };

      var data = {
        user_id: parseInt(
          CryptoJS.AES.decrypt(
            user.user_details.org_user_id,
            "anonymoose"
          ).toString(CryptoJS.enc.Utf8)
        ),
        hotline_id: conversation.hotline_id,
        conversation_id: convID,
        message: message,
        created_by: parseInt(
          CryptoJS.AES.decrypt(
            user.user_details.org_user_id,
            "anonymoose"
          ).toString(CryptoJS.enc.Utf8)
        ),
      };
      // Sending request for first message through API
      axios
        .post(BASE_URL + "receiveHotlineMessage/", data, options)
        .then((res) => {
          if (res.status === 201) {
            if (res.data.status_id === 1 || res.data.status_id === 3) {
              onChangeStatus(2, convID);
            }
            setSearchBy("");
            setCounter(res);
            setMessage("");
            setConvMsg({ ...convMsg, [convID]: "" });
            socket.send(JSON.stringify(res.data));
          }
        });
    }

    form.setFieldsValue({ chatArea: "" });
  };

  const connect = () => {
    setConvoMessages((convoMessages) => {
      let arrayOf = [];
      // Condition to pass the message to particular conv
      if (
        convoMessages &&
        convoMessages.length &&
        newState.conversation_id?.toString().replace(/(<([^>]+)>)/gi, "") ===
          convoMessages[0].conversation_id
            ?.toString()
            .replace(/(<([^>]+)>)/gi, "")
      ) {
        const filteredDataArray = convoMessages.filter(
          (item) =>
            item.conversation_id?.toString().replace(/(<([^>]+)>)/gi, "") ===
            newState.conversation_id?.toString().replace(/(<([^>]+)>)/gi, "")
        );
        arrayOf = [...filteredDataArray, newState];
      } else {
        arrayOf = [...convoMessages];
      }
      return arrayOf;
    });
  };

  const notesClicked = (e) => {
    var notes = document.getElementById("noteId");
    if (isMobile) {
      sethideDiv(true);
    }
    if (displayActivity.myClass != "") {
      setDisplayActivity({ myClass: "", activityDivClicked: false });
    }
    if (displayAssigne.myClass != "") {
      setDisplayAssigne({ myClass: "", assigneDivClicked: false });
    }
    if (displayNote.myClass === "") {
      notes.classList.add("selected");
      setDisplayNote({ myClass: "rgt-panel", noteDivClicked: true });
    } else {
      notes.classList.remove("selected");
      setDisplayNote({ myClass: "", noteDivClicked: false });
    }
    var activity = document.getElementById("activityId");
    activity.classList.remove("selected");
    var assigne = document.getElementById("assigneId");
    assigne.classList.remove("selected");
  };

  const activityClicked = (e) => {
    var activity = document.getElementById("activityId");
    if (isMobile) {
      sethideDiv(true);
    }
    if (displayNote.myClass != "") {
      setDisplayNote({ myClass: "", noteDivClicked: false });
    }
    if (displayAssigne.myClass != "") {
      setDisplayAssigne({ myClass: "", assigneDivClicked: false });
    }
    if (displayActivity.myClass === "") {
      activity.classList.add("selected");
      setDisplayActivity({ myClass: "rgt-panel", activityDivClicked: true });
    } else {
      activity.classList.remove("selected");
      setDisplayActivity({ myClass: "", activityDivClicked: false });
    }
    var notes = document.getElementById("noteId");
    notes.classList.remove("selected");
    var assigne = document.getElementById("assigneId");
    assigne.classList.remove("selected");
  };

  const assigneClicked = (e) => {
    var assigne = document.getElementById("assigneId");
    if (isMobile) {
      sethideDiv(true);
    }
    if (displayNote.myClass != "") {
      setDisplayNote({ myClass: "", noteDivClicked: false });
    }
    if (displayActivity.myClass != "") {
      setDisplayActivity({ myClass: "", activityDivClicked: false });
    }
    if (displayAssigne.myClass === "") {
      assigne.classList.add("selected");
      setDisplayAssigne({ myClass: "rgt-panel", assigneDivClicked: true });
    } else {
      assigne.classList.remove("selected");
      setDisplayAssigne({ myClass: "", assigneDivClicked: false });
    }
    var activity = document.getElementById("activityId");
    activity.classList.remove("selected");
    var notes = document.getElementById("noteId");
    notes.classList.remove("selected");
  };

  const createMarkup = (html) => {
    return { __html: html };
  };

  if (searchString) {
    if (conversation.responder_conversation_name.includes("</mark>")) {
      conversation.responder_conversation_name = conversation.responder_conversation_name
        ?.toString()
        .replace(/(<([^>]+)>)/gi, "");
      conversation.responder_conversation_name = conversation.responder_conversation_name.replace(
        new RegExp(searchString, "gi"),
        (match) =>
          `<mark style="background: #2769AA; color: white;">${match}</mark>`
      );
    }
    if (conversation.hotline_name.includes("</mark>")) {
      conversation.hotline_name = conversation.hotline_name
        ?.toString()
        .replace(/(<([^>]+)>)/gi, "");
      conversation.hotline_name = conversation.hotline_name.replace(
        new RegExp(searchString, "gi"),
        (match) =>
          `<mark style="background: #2769AA; color: white;">${match}</mark>`
      );
    }
    if (conversation.conversation_id?.toString().includes("</mark>")) {
      conversation.conversation_id = conversation.conversation_id
        ?.toString()
        .replace(/(<([^>]+)>)/gi, "");
      conversation.conversation_id = conversation.conversation_id
        ?.toString()
        .replace(
          new RegExp(searchString, "gi"),
          (match) =>
            `<mark style="background: #2769AA; color: white;">${match}</mark>`
        );
    }
    convoMessages.map((item) => {
      if (item.message.includes("</mark>")) {
        item.message = item.message?.toString().replace(/(<([^>]+)>)/gi, "");
        item.message = item.message.replace(
          new RegExp(searchString, "gi"),
          (match) =>
            `<mark style="background: #2769AA; color: white;">${match}</mark>`
        );
      } else {
        item.message = item.message.replace(/(?:\r\n|\r|\n)/g, "<br />");
        item.message = item.message.replace(
          new RegExp(searchString, "gi"),
          (match) =>
            `<mark style="background: #2769AA; color: white;">${match}</mark>`
        );
      }
      return {
        ...item,
        message: item.message,
      };
    });
  } else {
    conversation.responder_conversation_name = conversation.responder_conversation_name
      ?.toString()
      .replace(/(<([^>]+)>)/gi, "");
    conversation.hotline_name = conversation.hotline_name
      ?.toString()
      .replace(/(<([^>]+)>)/gi, "");
    conversation.conversation_id = conversation.conversation_id
      ?.toString()
      .replace(/(<([^>]+)>)/gi, "");
    convoMessages.map((item) => {
      item.message = item.message
        ?.toString()
        .replace(/(?:\r\n|\r|\n)/g, "<br />");
      return {
        ...item,
        message: item.message,
      };
    });
  }
  let messageLength = convoMessages.length - conversation.unread_messages_count;
  return (
    <Col
      span={24}
      lg={18}
      className={
        chatdiv ? "chat-section d-lg-flex" : "chat-section d-lg-flex d-none"
      }
      data-test="messages"
    >
      <Col span={24} className="h-100">
        <Row className="top-filter-pan p-2 p-lg-4 d-flex align-items-center">
          <Col span={24} lg={10}>
            <div className="">
              <h6
                dangerouslySetInnerHTML={createMarkup(
                  conversation.responder_conversation_name
                )}
              />
              <p
                style={{ margin: 0, display: "inline", float: "left" }}
                dangerouslySetInnerHTML={createMarkup(
                  conversation.hotline_name
                )}
              />
              &nbsp;|&nbsp;
              <p
                style={{ margin: 0, display: "inline", float: "center" }}
                dangerouslySetInnerHTML={createMarkup(
                  conversation.conversation_id
                )}
              />
            </div>
          </Col>
          <Col span={24} lg={14}>
            <div className="d-flex justify-content-end pt-3 pt-sm-1 pt-md-0">
              <div className="selection-panel pr-3">
                <label>{URGENCY_LEVEL}</label>
                <Select
                  bordered={false}
                  value={urgencyLevel}
                  onChange={(event) =>
                    onChangeUrgencyLevel(
                      event,
                      conversation.conversation_id
                        ?.toString()
                        .replace(/(<([^>]+)>)/gi, "")
                    )
                  }
                >
                  {obj_urgencyLevel.map((u) => (
                    <Option value={u.id}>{u.name}</Option>
                  ))}
                </Select>
              </div>
              <div className="selection-panel pr-3">
                <label>{STATUS}</label>
                <Select
                  bordered={false}
                  value={status}
                  onChange={(event) =>
                    onChangeStatus(
                      event,
                      conversation.conversation_id
                        ?.toString()
                        .replace(/(<([^>]+)>)/gi, "")
                    )
                  }
                >
                  {obj_status.map((s) =>
                    (status === 2 || status === 3) && s.id === 1 ? (
                      <Option value={s.id} disabled>
                        {s.name}
                      </Option>
                    ) : (
                      <Option value={s.id}>{s.name}</Option>
                    )
                  )}
                </Select>
              </div>
              <div className="icon-row d-flex align-items-center">
                <Col span={24} md={8}>
                  <span
                    id="noteId"
                    className="icon icon-create"
                    onClick={(e) => notesClicked(e)}
                  ></span>
                  {/* add selected class to activr icon */}
                </Col>
                <Col span={24} md={8}>
                  <span
                    id="activityId"
                    className="icon icon-activity"
                    onClick={(e) => activityClicked(e)}
                  ></span>
                </Col>
                <Col span={24} md={8}>
                  <span
                    id="assigneId"
                    className="icon icon-assign"
                    onClick={(e) => assigneClicked(e)}
                  ></span>
                </Col>
              </div>
            </div>
          </Col>
        </Row>
        {/* filter end */}

        <Row className="row-hgt">
          <div
            className={
              displayNote.noteDivClicked ||
              displayActivity.activityDivClicked ||
              displayAssigne.assigneDivClicked
                ? hideDiv
                  ? "w-75 d-none"
                  : "w-75"
                : "w-100"
            }
          >
            {/* add class w-75 when click on right side Note, Activity and Assign icon and remove once panel closed*/}
            <div className="msg_main">
              <Scrollbars autoHeightMax={450} universal={true}>
                <Col span={24} className="msg-history p-4">
                  {convoMessages.map((msg, index) => (
                    <div>
                      {messageLength === index ? (
                        <Divider plain>{LAST_READ}</Divider>
                      ) : null}
                      {msg.message.includes(
                        "Urgency Level has been changed to"
                      ) ? (
                        <Divider plain>{URGENCY_LEVEL_CHANGED}</Divider>
                      ) : null}
                      {msg.message.includes("Status has been changed to") ? (
                        <Divider plain>{STATUS_CHANGED}</Divider>
                      ) : null}
                      {msg.message.includes(
                        "Urgency Level has been changed to"
                      ) ||
                      msg.message.includes(
                        "Status has been changed to"
                      ) ? null : (
                        <div
                          className={
                            msg.is_responder_message
                              ? "outgoing-msg hotline_message mb-3"
                              : "incoming-msg hotline_message mb-2"
                          }
                        >
                          <div
                            className={
                              msg.is_responder_message
                                ? "sent-msg"
                                : "received-msg"
                            }
                          >
                            <p
                              ellipsis
                              // className="w-75 pr-2 mb-1"
                              dangerouslySetInnerHTML={createMarkup(
                                msg.message
                              )}
                            />

                            <span className="time-date">
                              {" "}
                              {moment(msg.created_on).isSame(moment(), "day")
                                ? moment(msg.created_on)
                                    .utc()
                                    .local()
                                    .format("hh:mm A")
                                : moment(msg.created_on)
                                    .utc()
                                    .local()
                                    .format("MM:DD:YY | hh:mm A ")}
                              {msg.is_responder_message &&
                                ` | ${msg.first_name} ${msg.last_name}`}
                            </span>
                          </div>
                        </div>
                      )}
                    </div>
                  ))}
                </Col>
              </Scrollbars>
            </div>
            <Col
              span={24}
              className={
                displayNote.noteDivClicked ||
                displayActivity.activityDivClicked ||
                displayAssigne.assigneDivClicked
                  ? "send-msg w-75"
                  : "send-msg w-100"
              }
            >
              {/* add class w-75 when click on right side Note, Activity and Assign icon and toggle with w-100 once panel closed*/}
              <div className="type-msg">
                <Form
                  className="input-md w-100"
                  name="validate-other"
                  form={form}
                >
                  <TextArea
                    className="input-control"
                    id="chatArea"
                    name={conversation.conversation_id}
                    placeholder={SEND_MESSAGE_TEXT}
                    onChange={(e) => handleChange(e)}
                    value={message}
                    onKeyDown={keyDown}
                    maxLength={10000}
                  />
                </Form>
                <span
                  className="ml-3 send"
                  onClick={() => onSendMessageClick()}
                >
                  <img src={Send} alt="send message" />
                </span>
              </div>
            </Col>
          </div>
          <div className="w-25 d-flex justify-content-end width-sm-100">
            {/* Converstaion Notes */}
            {displayNote.noteDivClicked ? (
              <Note
                class={displayNote.myClass}
                conversation_id={conversation.conversation_id
                  ?.toString()
                  .replace(/(<([^>]+)>)/gi, "")}
                notesClicked={() => notesClicked()}
              />
            ) : null}
            {displayActivity.activityDivClicked ? (
              <Activity
                class={displayActivity.myClass}
                conversation_id={conversation.conversation_id
                  ?.toString()
                  .replace(/(<([^>]+)>)/gi, "")}
                activityClicked={() => activityClicked()}
              />
            ) : null}
            {displayAssigne.assigneDivClicked ? (
              <Assigned
                class={displayAssigne.myClass}
                hotline_id={conversation.hotline_id}
                assigneClicked={() => assigneClicked()}
              />
            ) : null}
          </div>
        </Row>
      </Col>
    </Col>
  );
};

export default Messages;
