import React from "react";
import { Layout, Row, Col, Input, Spin, notification } from "antd";
import { LoggedinHeader } from "../../containers";
import "../manageEmp/manageEmp.scss";
import Grid from "../table/Table";
import Buttons from "../button/Button";
import { AudioOutlined, PaperClipOutlined } from "@ant-design/icons";
import { Link } from "react-router-dom";
import OrgSidebar from "../../containers/OrgSidebar";
import moment from "moment";
import PollPopover from "../table/PollPopover";
import CreatePollPopover from "../table/CreatePollPopover";
import authHeader from "../../services/auth-header";
import axios from "axios";
import momentUtil from "../../utils/momentUtil";
import { BASE_URL } from "../../userconfig";
import { CREATE_POLL, MANAGE_POLLS, SEARCH } from "../../headings";

const { Search } = Input;

const suffix = (
  <AudioOutlined
    style={{
      fontSize: 16,
      color: "#1890ff",
    }}
  />
);
const { Content } = Layout;

class ShowAllPolls extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      filterData: [],
      searchBy: "",
    };
  }

  componentDidMount() {
    let { polls } = this.props;
    polls?.forEach((poll) => {
      poll.start_date = moment(poll.start_date)
        .utc()
        .local()
        .format("MM/DD/YYYY");
      poll.end_date = moment(poll.end_date).utc().local().format("MM/DD/YYYY");
    });
    this.setState({ data: polls }, () => {
      this.onSearch("");
    });
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (this.props.polls !== nextProps.polls) {
      nextProps.polls?.forEach((poll) => {
        poll.start_date = moment(poll.start_date)
          .utc()
          .local()
          .format("MM/DD/YYYY");
        poll.end_date = moment(poll.end_date)
          .utc()
          .local()
          .format("MM/DD/YYYY");
      });
      this.setState({ data: nextProps.polls }, () => {
        this.onSearch(this.state.searchBy);
      });
    }
  }

  filterIt(arr, searchKey) {
    if (!searchKey || searchKey === "" || searchKey === null) {
      return arr;
    }
    return arr.filter(function (obj) {
      return Object.keys(obj).some(function (key) {
        if (typeof obj[key] === "string") {
          return obj[key].toUpperCase().includes(searchKey.toUpperCase());
        }
      });
    });
  }

  extendPollDate = (value, date) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    const data = {
      end_date: momentUtil.formatAsDDMMYYYY(date.pollEndDate),
      end_time: momentUtil.formatAsHHmmss(date.pollEndTime),
    };
    axios
      .put(BASE_URL + "extendEndDate/" + value.poll_id + "/", data, options)
      .then(() => {
        notification.config({
          placement: "bottomLeft",
        });
        notification.open({
          description: `${value.pollName} date has been extended successfully.`,
        });
        window.location = "/orgcc/pollsdirectory";
      });
  };

  onSearch = (value) => {
    const { data } = this.state;
    const filterDt = this.filterIt(data, value);
    this.setState({ filterData: filterDt });
  };

  statusClicked = (record, via) => {
    let data = {};
    if (via.length === 0) {
      notification.config({
        placement: "bottomLeft",
      });
      notification.open({
        description: `Status of ${record.title} can't be changed as this poll is completed.`,
      });
    } else if (via === "Scheduled" || via === "Active") {
      data = {
        status_id: 2,
      };
    } else if (via === "inActive") {
      const startDate = record.start_date;
      const sdate = startDate.split("/")[0];
      const smonth = startDate.split("/")[1];
      const syear = startDate.split("/")[2];
      const finalStartDate = new Date(smonth + "-" + sdate + "-" + syear);
      const currentDate = new Date();
      if (finalStartDate > currentDate) {
        data = {
          status_id: 3,
        };
      } else if (finalStartDate < currentDate) {
        data = {
          status_id: 1,
        };
      } else if (finalStartDate === currentDate) {
        const startTime = record.start_time;
        const SFullDate = smonth + "-" + sdate + "-" + syear;
        const startTimeWithDate = new Date(SFullDate + startTime);
        if (startTimeWithDate > new Date()) {
          data = {
            status_id: 3,
          };
        } else if (startTimeWithDate <= new Date()) {
          data = {
            status_id: 1,
          };
        }
      }
    }
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios
      .put(BASE_URL + "changePollStatus/" + record.poll_id + "/", data, options)
      .then(() => {
        notification.config({
          placement: "bottomLeft",
        });
        notification.open({
          description: `Status of ${record.title} has been changed successfully.`,
        });
        this.props.statusOnClicked();
      });
  };

  render() {
    const { polls, loader } = this.props;
    const columns = [
      {
        title: "",
        dataIndex: "",
        key: "name",
        width: "5%",
        render: (record) => {
          return {
            children:
              record.image || record.document ? <PaperClipOutlined /> : null,
            props: {
              "data-title": "",
            },
          };
        },
      },
      {
        title: "Poll Name",
        dataIndex: "title",
        key: "name",
        width: "22%",
        render: (name, record) => {
          return {
            children: (
              <Link
                to={{
                  pathname: `/orgcc/editpoll/${record.poll_id}`,
                  state: record,
                }}
              >
                <li>
                  <a href={`/orgcc/editpoll/${record.poll_id}`}>{name}</a>{" "}
                </li>
              </Link>
            ),
            props: {
              "data-title": "Poll Name",
            },
          };
        },
      },
      {
        title: "Start Date & Time",
        dataIndex: "",
        key: "start_date",
        width: "22%",
        render: (record) => {
          return {
            children: record.start_date + " " + record.start_time.slice(0, -3),
            props: {
              "data-title": "Start Date & Time",
            },
          };
        },
      },
      {
        title: "End Date & Time",
        dataIndex: "",
        key: "end_date",
        width: "21%",
        render: (record) => {
          return {
            children: record.end_date + " " + record.end_time.slice(0, -3),
            props: {
              "data-title": "Start Date & Time",
            },
          };
        },
      },
      {
        title: "Status",
        dataIndex: "",
        key: "x",
        width: "10%",
        render: (record) => {
          return {
            children:
              record.status_id === 2 ? (
                <div
                  className="secondary-tag-rounded tag"
                  onClick={() => this.statusClicked(record, "inActive")}
                >
                  In active
                </div>
              ) : record.status_id === 1 ? (
                <div
                  className="primary-tag-rounded tag"
                  onClick={() => this.statusClicked(record, "Active")}
                >
                  <span className="circle mr-1"></span>
                  Active
                </div>
              ) : record.status_id === 3 ? (
                <div
                  className="primary-tag-rounded tag"
                  onClick={() => this.statusClicked(record, "Scheduled")}
                >
                  <span className="circle mr-1"></span>
                  Scheduled
                </div>
              ) : (
                <div
                  className="primary-tag-rounded tag"
                  onClick={() => this.statusClicked(record, "")}
                >
                  <span className="circle mr-1"></span>
                  Completed
                </div>
              ),
            props: {
              "data-title": "Status",
            },
          };
        },
      },
      {
        title: "",
        dataIndex: "",
        key: "x",
        width: "5%",
        render: (record) => {
          return {
            children: (
              <PollPopover extendPollDate={this.extendPollDate} data={record} />
            ),
            props: {
              "data-title": "Action",
            },
          };
        },
      },
    ];

    return (
      <>
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Spin spinning={loader} size="large">
          <Row className="content-center pt-0">
            <Layout>
              <Layout>
                <OrgSidebar amORGUser data={this.props.data} />
                <Content className="">
                  <Row className="py-3">
                    <h1>{MANAGE_POLLS}</h1>
                  </Row>
                  <Col span={24}>
                    <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
                      <Col
                        span={24}
                        lg={18}
                        className="search-row mt-2 mt-lg-0"
                      >
                        <Row>
                          <Col span={24} lg={9} className="search-box">
                            <Search
                              placeholder={SEARCH}
                              onSearch={this.onSearch}
                            />
                          </Col>
                        </Row>
                      </Col>

                      <Col
                        span={24}
                        lg={6}
                        className="d-flex align-items-center justify-content-end"
                      >
                        {/* <CreatePollPopover polls={polls} /> */}
                        <Link to="/orgcc/createpoll">
                          <Buttons value={CREATE_POLL} />
                        </Link>
                      </Col>
                    </Row>
                  </Col>
                  <Grid
                    rowKey="poll_id"
                    columns={columns}
                    data={this.state.filterData}
                  />
                </Content>
              </Layout>
            </Layout>
          </Row>
        </Spin>
      </>
    );
  }
}

export default ShowAllPolls;
