import React from "react";
import { Popover, Button } from "antd";
import DeleteModal from "../../components/modal/DeleteModal";
import dotsicon from "../../assets/icons/dots-icon.svg";

const content = (props) => (
  <div>
    <ul>
      <li>
        {" "}
        <a href="/orgcc/createpoll">Create New</a>{" "}
      </li>

      {/* <li>
        <a href="/orgcc/existingpolls">Choose from existing</a>{" "}
      </li> */}
    </ul>
  </div>
);

class CreatePollPopover extends React.Component {
  state = {
    visible: false,
  };

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (this.props !== nextProps && this.state.visible) {
      this.hide();
    }
  }

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  handleVisibleChange = (visible) => {
    this.setState({ visible });
  };

  render() {
    return (
      <>
        <Popover
          placement="bottom"
          content={content(this.props)}
          trigger="click"
          visible={this.state.visible}
          onVisibleChange={this.handleVisibleChange}
        >
          <Button type="primary">{CREATE_POLL}</Button>
        </Popover>
      </>
    );
  }
}

export default CreatePollPopover;
