import React from "react";
import { Row, Col, Input, Form, Select, Modal, Alert, DatePicker } from "antd";
import FloatLabel from "./FloatLabels";
import "react-phone-input-2/lib/style.css";
import PhoneInput from "react-phone-input-2";
import {ADD_SEEDED_USER, CANCEL_MODAL, ADD_TEXT_MODAL} from "../../headings";
import {REQUIRED_MESSAGE, MAX_CHAR_LIMIT, VALID_EMAIL, VALID_MOBILE} from "../../errors";
import {FIRST_NAME, LAST_NAME, EMAIL, DATE, MOBILE, STATUS} from "../../labels";
import {STATUS_ACTIVE, STATUS_INACTIVE} from "../../values";

const { RangePicker } = DatePicker;

const AddNewSeededUser = (props) => {
  return (
    <Modal
      visible={true}
      onOk={props.handleAddSeededUser}
      onCancel={props.handleAddSeededUserCancel}
      centered
      width={1200}
      okButtonProps={{
        children: "Custom OK",
      }}
      cancelButtonProps={{
        children: "Custom cancel",
      }}
      okText={ADD_TEXT_MODAL}
      cancelText={CANCEL_MODAL}
    >
      <Form className="input-md floating-label w-100" name="validate_other">
        {props.error && (
          <Form.Item>
            <Alert
              message={REQUIRED_MESSAGE}
              type="error"
              showIcon
            ></Alert>
          </Form.Item>
        )}
        <Col span={24}>
          <div>{ADD_SEEDED_USER}</div>
          <br />
          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
            <Col span={24} md={8} className="gutter-row">
              <FloatLabel
                label={FIRST_NAME}
                value={props.seededUserDetails.firstName}
              >
                <Form.Item name="firstName" className="required">
                  <Input
                    id="firstName"
                    className="input-control"
                    defaultValue={props.seededUserDetails.firstName}
                    htmlFor="firstName"
                    value={props.seededUserDetails.firstName}
                    onChange={(e) => props.handleSeededUserOnChange(e)}
                    autoComplete="off"
                    maxLength={50}
                  />
                </Form.Item>
                {props.fNameCount && (
                  <Form.Item>
                    <Alert
                      message={MAX_CHAR_LIMIT}
                      type="error"
                    ></Alert>
                  </Form.Item>
                )}
              </FloatLabel>
            </Col>
            <Col span={24} md={8} className="gutter-row">
              <FloatLabel
                label={LAST_NAME}
                value={props.seededUserDetails.lastName}
              >
                <Form.Item name="lastName" className="required">
                  <Input
                    className="input-control"
                    id="lastName"
                    htmlFor="lastName"
                    className="required"
                    defaultValue={props.seededUserDetails.lastName}
                    value={props.seededUserDetails.lastName}
                    onChange={(e) => props.handleSeededUserOnChange(e)}
                    autoComplete="off"
                    maxLength={50}
                  />
                </Form.Item>
                {props.lNameCount && (
                  <Form.Item>
                    <Alert
                      message={MAX_CHAR_LIMIT}
                      type="error"
                    ></Alert>
                  </Form.Item>
                )}
              </FloatLabel>
            </Col>
            {/* <Col span={24} md={8} className="gutter-row">
              <FloatLabel
                label="Temporary Password"
                value={props.seededUserDetails.password}
              >
                <Form.Item name="password" className="pwd required">
                  <Input.Password
                    className="input-control"
                    htmlFor="password"
                    className="required"
                    id="password"
                    defaultValue={props.seededUserDetails.password}
                    value={props.seededUserDetails.password}
                    onChange={(e) => props.handleSeededUserOnChange(e)}
                    onBlur={(e) => props.validatePassword(e)}
                    autoComplete="new-password"
                  />
                  {props.passwordValidationError && (
                    <Alert
                      message="Please enter a valid password.
                      It should be atlast 8 characters long with atleast 1 each of Capital letter, Small letter, Numeric and Special character."
                      type="error"
                      showIcon
                    ></Alert>
                  )}
                </Form.Item>
              </FloatLabel>
            </Col> */}
            <Col span={24} md={8} className="gutter-row">
              <FloatLabel
                label={EMAIL}
                value={props.seededUserDetails.email}
              >
                <Form.Item name="email" className="required">
                  <Input
                    className="input-control"
                    id="email"
                    htmlFor="email"
                    className="required"
                    defaultValue={props.seededUserDetails.email}
                    value={props.seededUserDetails.email}
                    onChange={(e) => props.handleSeededUserOnChange(e)}
                    onBlur={(e) => props.validateEmailAddress(e)}
                    autoComplete="off"
                  />
                  {props.emailValidationError && (
                    <Alert
                      message={VALID_EMAIL}
                      type="error"
                      showIcon
                    ></Alert>
                  )}
                </Form.Item>
              </FloatLabel>
            </Col>
            <Col span={24} md={8} className="gutter-row">
              <FloatLabel label={DATE} value="date2">
                <Form.Item className="input-control datepicker-wrapper required">
                  <RangePicker
                    className="new w-100"
                    name="date2"
                    className="required"
                    value={props.seededUserDetails.date2}
                    onChange={(date) =>
                      props.handleSeededUserDatePickerChange(date, "date2")
                    }
                    disabledDate={props.disabledDate}
                  />
                </Form.Item>
              </FloatLabel>
            </Col>
            <Col span={24} md={8} className="gutter-row phone-field">
              <FloatLabel label={MOBILE} value={props.seededUserDetails.mobile}>
                <Form.Item name="mobile" className="required">
                  <PhoneInput
                    country={"us"}
                    id="mobile"
                    htmlFor="mobile"
                    value={props.seededUserDetails.mobile}
                    onChange={props.handleMobile}
                    onBlur={props.validateMobile}
                    autoComplete="off"
                  />
                  {props.mobileValidationError && (
                    <Alert
                      message={VALID_MOBILE}
                      type="error"
                      showIcon
                    ></Alert>
                  )}
                </Form.Item>
              </FloatLabel>
            </Col>
            <Col span={24} md={8} className="gutter-row">
              <FloatLabel
                label={STATUS}
                value={props.seededUserDetails.status2}
              >
                <Form.Item className="required" name="status2">
                  <>
                    <Select
                      className="input-control w-100 w-lg-50"
                      value={props.seededUserDetails.status2}
                      htmlFor="status2"
                      className="required"
                      onSelect={(value) =>
                        props.handleSeededUserOnSelect(value, "status2")
                      }
                    >
                      <Option value="1">{STATUS_ACTIVE}</Option>
                      <Option value="2">{STATUS_INACTIVE}</Option>
                    </Select>
                  </>
                </Form.Item>
              </FloatLabel>
            </Col>
          </Row>
        </Col>
      </Form>
    </Modal>
  );
};

export default AddNewSeededUser;
