import React, { useState } from "react";
import { Layout, Row, Col, Divider, Button } from "antd";
import { LoggedinHeader } from "../../containers";
import "../hotlineDirectory/hotlineDirectory.scss";
import CreateHotline from "../../assets/images/create-hotline.svg";
import OpenlineCreation from "../../components/openlineCreation/OpenlineCreation";
import OrgSidebar from "../../containers/OrgSidebar";
import { CREATE, OPENLINE_DIRECTORY } from "../../headings";
import {
  CREATE_NEW_OPENLINE_TEXT_PART1,
  CREATE_NEW_OPENLINE_TEXT_PART2,
} from "../../messages";

const { Content } = Layout;

const OpenlineDirectory = (props) => {
  const [showResult, setShowResult] = useState(false);

  const showBtn = () => {
    setShowResult(true);
  };
  return (
    <React.Fragment>
      {!showResult === true ? (
        <React.Fragment>
          {" "}
          <Row className="inner-header">
            <LoggedinHeader amORGUser />
          </Row>
          <Row className="content-center pt-0">
            <Layout>
              <Layout>
                <OrgSidebar amORGUser data={props.data} />
                <Content data-test="Directory" className="">
                  <Row className="py-3">
                    <h1 className="mb-0">{OPENLINE_DIRECTORY}</h1>
                  </Row>
                  <div className="mid-content d-flex justify-content-center align-items-center">
                    <Row>
                      <Col span={24}>
                        <div className="c-vector-area">
                          <img
                            src={CreateHotline}
                            alt="Create Hotline"
                            className="pr-4"
                          />
                        </div>
                        <p className="text-center pt-3">
                          {CREATE_NEW_OPENLINE_TEXT_PART1}
                          <br />
                          {CREATE_NEW_OPENLINE_TEXT_PART2}
                        </p>
                        <Row className="d-flex justify-content-center">
                          <Divider className="mt-1" />
                        </Row>
                        <Col
                          span={24}
                          className="d-flex align-items-center justify-content-center"
                        >
                          <Button
                            data-test="createHotline"
                            onClick={showBtn}
                            type="primary"
                          >
                            {CREATE}
                          </Button>
                        </Col>
                      </Col>
                    </Row>
                  </div>
                </Content>
              </Layout>
            </Layout>
          </Row>
        </React.Fragment>
      ) : (
        <OpenlineCreation />
      )}
    </React.Fragment>
  );
};

export default OpenlineDirectory;
