import React, { useState } from "react";
import { Layout, Row, Col, Divider, Button } from "antd";
import { LoggedinHeader } from "../../containers";
import "./managePoll.module.scss";
import { Redirect } from "react-router-dom";
import ManagePolls from "../../assets/images/manage-polls.svg";
import OrgSidebar from "../../containers/OrgSidebar";
import CreatePoll from "../managePolls/CreatePoll";
import { CREATE_POLL, MANAGE_POLLS } from "../../headings";
import { CREATE_NEW_POLL_TEXT } from "../../messages";

const { Content } = Layout;
const ManagePoll = (props) => {
  const [createPoll, setCreatePoll] = useState(false);
  const ShowCreatePoll = () => {
    setCreatePoll(true);
  };

  return (
    <React.Fragment>
      {!createPoll ? (
        <React.Fragment>
          {" "}
          <Row className="inner-header">
            <LoggedinHeader amORGUser />
          </Row>
          <Row className="content-center pt-0">
            <Layout>
              <Layout>
                <OrgSidebar amORGUser data={props.data} />
                <Content data-test="Polls" className="">
                  <Row className="py-3">
                    <h1 className="mb-0">{MANAGE_POLLS}</h1>
                  </Row>
                  <div className="mid-content d-flex justify-content-center align-items-center">
                    <Row>
                      <Col span={24}>
                        <div className="c-vector-area">
                          <img
                            src={ManagePolls}
                            alt="Create Poll"
                            className="pr-4"
                          />
                        </div>
                        <p className="text-center pt-3">
                          {CREATE_NEW_POLL_TEXT}
                        </p>
                        <Row className="d-flex justify-content-center">
                          <Divider className="mt-1" />
                        </Row>
                        <Col
                          span={24}
                          className="d-flex align-items-center justify-content-center"
                        >
                          <Button
                            data-test="createHotline"
                            onClick={ShowCreatePoll}
                            type="primary"
                          >
                            {CREATE_POLL}
                          </Button>
                        </Col>
                      </Col>
                    </Row>
                  </div>
                </Content>
              </Layout>
            </Layout>
          </Row>
        </React.Fragment>
      ) : (
        <Redirect to="/orgcc/createpoll" />
        // <CreatePoll data={props.data} />
      )}
    </React.Fragment>
  );
};

export default ManagePoll;
