import React, { Component } from "react";
import { Modal, Button, Form, Col, DatePicker, Alert } from "antd";
import FloatLabel from "../form/FloatLabels";
import moment from "moment";
import "../modal/modal.scss";
import Grid from "@material-ui/core/Grid";
import "date-fns";
import DateFnsUtils from "@date-io/date-fns";
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
} from "@material-ui/pickers";
import { PAST_TIME, SELECT_DATE } from "../../errors";
import {
  CANCEL,
  PUBLISH,
  RESCHEDULE,
  SCHEDULE,
  PUBLISH_POLL,
} from "../../headings";
import { POLL_DATES_TEXT } from "../../messages";
import {
  SELECT_START_DATE,
  SELECT_END_DATE,
  SELECT_START_TIME,
  SELECT_END_TIME,
} from "../../labels";

class DatetimeModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      startDate: "",
      startTime: new Date(),
      endDate: "",
      endTime: new Date(),
      startTimeError: false,
      endTimeError: false,
    };
  }

  componentDidMount() {
    let {
      pollStartDate,
      pollStartTime,
      pollEndDate,
      pollEndTime,
    } = this.props.pollData;
    const sdate = pollStartDate.split("-")[0];
    const smonth = pollStartDate.split("-")[1];
    const syear = pollStartDate.split("-")[2];
    const edate = pollEndDate.split("-")[0];
    const emonth = pollEndDate.split("-")[1];
    const eyear = pollEndDate.split("-")[2];
    this.setState({
      startDate: moment(pollStartDate, "DD-MM-YYYY"),
      endDate: moment(pollEndDate, "DD-MM-YYYY"),
      startTime: new Date(
        syear + "-" + smonth + "-" + sdate + "T" + pollStartTime
      ),
      endTime: new Date(eyear + "-" + emonth + "-" + edate + "T" + pollEndTime),
    });
  }

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = () => {
    if (
      !this.props.pollData.pollStartDate ||
      !this.props.pollData.pollEndDate
    ) {
      this.props.showError(true);
    } else {
      this.props.showError(false);
      this.setState({
        visible: false,
      });
      this.props.onPublish();
    }
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
  };

  disabledStartDate = (current) => {
    return current && current < moment().startOf("day");
  };

  disabledEndDate = (current) => {
    if (this.props.pollData.pollStartDate) {
      return (
        current &&
        current < moment(this.props.pollData.pollStartDate, "DD-MM-YYYY")
      );
    } else {
      return current && current < moment().startOf("day");
    }
  };

  range = (start, end) => {
    const result = [];
    for (let i = start; i < end; i++) {
      result.push(i);
    }
    return result;
  };

  onChangeStartDate = (date) => {
    this.props.handlePollStartDatePickerChange(date, "pollStartDate");
  };

  onChangeEndDate = (date) => {
    this.props.handlePollEndDatePickerChange(date, "pollEndDate");
  };

  onChangeStartTime = (time) => {
    const currentTime = new Date();
    if (currentTime > time) {
      this.setState({ startTimeError: true });
    } else {
      this.setState({ startTimeError: false, startTime: time });
      this.props.handlePollStartTimePickerChange(time, "pollStartTime");
    }
  };

  onChangeEndTime = (time) => {
    const currentTime = new Date();
    if (currentTime > time) {
      this.setState({ endTimeError: true });
    } else {
      this.setState({ endTime: time, endTimeError: false });
      this.props.handlePollEndTimePickerChange(time, "pollEndTime");
    }
  };

  render() {
    return (
      <>
        <Button type="primary" className="md mr-4" onClick={this.showModal}>
          {this.props.viaEdit ? RESCHEDULE : SCHEDULE}
        </Button>
        <Modal
          title={PUBLISH_POLL}
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={520}
          okButtonProps={
            this.props.pollData.status_id === 4
              ? {
                  style: { display: "none" },
                }
              : {
                  children: "Custom OK",
                }
          }
          cancelButtonProps={
            this.props.pollData.status_id === 4
              ? {
                  className: "ant-btn-primary",
                  children: "Custom cancel",
                }
              : {
                  className: "ant-btn-secondary",
                  children: "Custom cancel",
                }
          }
          okText={PUBLISH}
          cancelText={CANCEL}
        >
          <Form className="input-md floating-label w-100">
            <Col span={24} className="gutter-row">
              {this.props.error && (
                <Form.Item>
                  <Alert message={SELECT_DATE} type="error" showIcon></Alert>
                </Form.Item>
              )}
              <div className="mb-4 subtext-md">
                {POLL_DATES_TEXT}
                <span className="pr-1 pr-1 font-weight-md6">
                  {" "}
                  {this.props.pollData.pollName}
                </span>
                Poll
              </div>

              <FloatLabel label={SELECT_START_DATE} value="sDate">
                <Form.Item
                  name="sDate"
                  rules={[
                    {
                      required: true,
                      message: SELECT_DATE,
                    },
                  ]}
                >
                  <DatePicker
                    defaultValue={this.state.startDate}
                    disabledDate={this.disabledStartDate}
                    onChange={(date) => this.onChangeStartDate(date)}
                    className="w-100"
                    name="sDate"
                    className="required"
                    disabled={this.props.pollData.status_id === 4}
                  />
                  {this.state.startTimeError && (
                    <Alert message={PAST_TIME} type="error" showIcon></Alert>
                  )}
                  <MuiPickersUtilsProvider utils={DateFnsUtils}>
                    <Grid container justify="space-around">
                      <KeyboardTimePicker
                        margin="normal"
                        id="time-picker1"
                        label={SELECT_START_TIME}
                        value={this.state.startTime}
                        onChange={(time) => this.onChangeStartTime(time)}
                        KeyboardButtonProps={{
                          "aria-label": "change time",
                        }}
                        disabled={this.props.pollData.status_id === 4}
                      />
                    </Grid>
                  </MuiPickersUtilsProvider>
                </Form.Item>
              </FloatLabel>
              <FloatLabel label={SELECT_END_DATE} value="eDate">
                <Form.Item
                  name="eDate"
                  rules={[
                    {
                      required: true,
                      message: SELECT_DATE,
                    },
                  ]}
                >
                  {this.state.endDate !== "" ? (
                    <>
                      {" "}
                      <DatePicker
                        defaultValue={this.state.endDate}
                        disabledDate={this.disabledEndDate}
                        onChange={(date) => this.onChangeEndDate(date)}
                        className="w-100"
                        name="eDate"
                        className="required"
                        disabled={this.props.pollData.status_id === 4}
                      />
                    </>
                  ) : (
                    <DatePicker
                      defaultValue={this.state.endDate}
                      disabledDate={this.disabledEndDate}
                      onChange={(date) => this.onChangeEndDate(date)}
                      className="w-100"
                      name="eDate"
                      className="required"
                      disabled={this.props.pollData.status_id === 4}
                    />
                  )}
                  {this.state.endTimeError && (
                    <Alert message={PAST_TIME} type="error" showIcon></Alert>
                  )}
                  <MuiPickersUtilsProvider utils={DateFnsUtils}>
                    <Grid container justify="space-around">
                      <KeyboardTimePicker
                        margin="normal"
                        id="time-picker2"
                        label={SELECT_END_TIME}
                        value={this.state.endTime}
                        onChange={(time) => this.onChangeEndTime(time)}
                        KeyboardButtonProps={{
                          "aria-label": "change time",
                        }}
                        disabled={this.props.pollData.status_id === 4}
                      />
                    </Grid>
                  </MuiPickersUtilsProvider>
                </Form.Item>
              </FloatLabel>
            </Col>
          </Form>
        </Modal>
      </>
    );
  }
}

export default DatetimeModal;
