import React, { useEffect, useState } from "react";
import {
  Form,
  Select,
  Input,
  Button,
  DatePicker,
  Alert,
  Row,
  Layout,
  notification,
} from "antd";
import AddIcoModal from "../../components/modal/AddIcoModal";
import ReactHtmlParser from "react-html-parser";
import * as AppStorage from "../../utils/AppStorage";
import FloatLabel from "../form/FloatLabels";
import moment from "moment";
import authHeader from "../../services/auth-header";
import axios from "axios";
import { CheckCircleTwoTone } from "@ant-design/icons";
import { LoggedinHeader } from "../../containers";
import OrgSidebar from "../../containers/OrgSidebar";
import { useLocation } from "react-router-dom";
import "../form/form.scss";
import { BASE_URL } from "../../userconfig";
import {
  REQUIRED_MESSAGE,
  STATUS_NOT_CHANGED,
  ENTER_DIRECTORY_NAME,
  HOTLINE_ALREADY_EXISTS,
  SELECT_DATE,
  ENTER_DESCRIPTION,
} from "../../errors";
import { HOTLINE_UPDATED } from "../../messages";
import { CANCEL, EDIT_HOTLINE, UPDATE } from "../../headings";
import {
  STATUS_ACTIVE,
  STATUS_INACTIVE,
  CYBER_BULLYING,
  DISCRIMINATION,
  FIGHT,
  RACISM,
  SEXUAL_HARRASMENT,
  SLAP,
  VIOLENCE,
} from "../../values";
import { DATE, DESCRIPTION, DIRECTORY_NAME, STATUS } from "../../labels";

var CryptoJS = require("crypto-js");
const { TextArea } = Input;
const { Option } = Select;
const { Content } = Layout;
const EditHotLine = (props) => {
  const [hotLineData, sethotLineData] = useState({
    end_date: "",
    help_text: "",
    hotline_icon_id: "",
    hotline_id: "",
    name: "",
    start_date: "",
    status_id: "",
  });
  const [hotlineStatus, setHotlineStatus] = useState("");
  const [iconHtml, seticonHtml] = useState("");
  const [hotlineErrorUpdated, sethotlineErrorUpdated] = useState("");
  const [api, contextHolder] = notification.useNotification();
  const [error, showError] = useState(false);

  const [disabledbtn, setdisabledbtn] = useState(true);
  const [form] = Form.useForm();
  let location = useLocation();

  const addIcon = (values, id) => {
    sethotLineData({
      ...hotLineData,
      hotline_icon_id: id,
    });
    seticonHtml(values);
  };

  const getIconText = (id) => {
    switch (id) {
      case "1":
        return SEXUAL_HARRASMENT;
      case "2":
        return VIOLENCE;
      case "3":
        return RACISM;
      case "4":
        return CYBER_BULLYING;
      case "5":
        return FIGHT;
      case "6":
        return DISCRIMINATION;
      case "7":
        return SLAP;
      default:
        return "";
    }
  };

  const setIcon = (id) => {
    switch (id) {
      case "1":
        return '<span class="icon icon-sexual-harassment-1 ant-tooltip-open"></span>';
      case "2":
        return '<span class="icon icon-violence ant-tooltip-open"></span>';
      case "3":
        return '<span class="icon icon-racism ant-tooltip-open"></span>';
      case "4":
        return '<span class="icon icon-cyber-bullying ant-tooltip-open"></span>';
      case "5":
        return '<span class="icon icon-fight ant-tooltip-open"></span>';
      case "6":
        return '<span class="icon icon-discrimination ant-tooltip-open"></span>';
      case "7":
        return '<span class="icon icon-slap ant-tooltip-open"></span>';
      default:
        return "";
    }
  };

  useEffect(() => {
    const sDate = moment(location.state.start_date, "MM / DD / YYYY").format(
      "YYYY-MM-DD"
    );
    const eDate = moment(location.state.end_date, "MM / DD / YYYY").format(
      "YYYY-MM-DD"
    );
    let dates = [moment(sDate, "YYYY/MM/DD"), moment(eDate, "YYYY/MM/DD")];
    sethotLineData({
      end_date: "",
      help_text: location.state.help_text,
      hotline_icon_id: location.state.hotline_icon_id,
      hotline_id: location.state.hotline_id,
      name: location.state.name,
      start_date: dates,
      status_id: location.state.status_id.toString(),
    });
    setHotlineStatus(location.state.status_id.toString());
    const test = setIcon(location.state.hotline_icon_id.toString());
    seticonHtml(test);
    form.setFieldsValue({
      name: location.state.name,
      help_text: location.state.help_text,
    });
  }, []);

  const handleStepOneOnChange = (e) => {
    sethotLineData({ ...hotLineData, [e.target.id]: e.target.value });
  };

  const handleStepOneDatePickerChange = (date, name) => {
    sethotLineData({ ...hotLineData, [name]: date });
  };

  const handleStepOneOnSelect = (value, name) => {
    sethotLineData({ ...hotLineData, [name]: value });
  };

  const editHotline = (e) => {
    if (
      hotLineData.name &&
      hotLineData.name.replace(/^\s*/, "").replace(/\s*$/, "").length > 0 &&
      hotLineData.help_text &&
      hotLineData.help_text.replace(/^\s*/, "").replace(/\s*$/, "").length >
        0 &&
      hotLineData.status_id &&
      hotLineData.hotline_icon_id &&
      hotLineData.start_date
    ) {
      showError(false);
      e.preventDefault();
      const user = JSON.parse(AppStorage.get("user"));
      const endDate = moment(hotLineData.start_date[1]).format("MM-DD-YYYY");
      const data = {
        end_date: moment(hotLineData.start_date[1]).format("DD-MM-YYYY"),
        help_text: hotLineData.help_text,
        hotline_icon_id: hotLineData.hotline_icon_id,
        hotline_id: hotLineData.hotline_id,
        name: hotLineData.name,
        start_date: moment(hotLineData.start_date[0]).format("DD-MM-YYYY"),
        status_id: parseInt(hotLineData.status_id),
        updated_by: parseInt(
          CryptoJS.AES.decrypt(
            user.user_details.org_user_id,
            "anonymoose"
          ).toString(CryptoJS.enc.Utf8)
        ),
      };
      if (
        hotlineErrorUpdated == "status: false" ||
        hotLineData.name === location.state.name
      ) {
        if (hotlineStatus !== hotLineData.status_id) {
          if (new Date() < new Date(endDate)) {
            saveHotLine(data);
          } else {
            const placement = "bottomLeft";
            api.info({
              description: STATUS_NOT_CHANGED,
              placement,
            });
          }
        } else {
          saveHotLine(data);
        }
      }
    } else {
      showError(true);
    }
  };

  const cancelHotline = (e) => {
    window.location = "/orgcc/hotlinedirectory";
  };

  const saveHotLine = (data) => {
    const token = authHeader();
    let hotLineId = hotLineData.hotline_id;
    delete hotLineData.hotline_id;
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios.put(BASE_URL + "hotline/" + hotLineId, data, options).then((res) => {
      if (res.status === 200) {
        const placement = "bottomLeft";
        api.info({
          description: HOTLINE_UPDATED,
          placement,
        });
        window.location = "/orgcc/hotlinedirectory";
      }
    });
  };

  const disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  const CheckHotlineName = (e) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    if (e.target.value !== location.state.name) {
      axios
        .post(
          BASE_URL + "checkhotline/",
          { hotline_name: e.target.value },
          options
        )
        .then((res) => {
          if (res.data) {
            sethotlineErrorUpdated(res.data[0]);
          }
        });
    } else {
      sethotlineErrorUpdated(false);
    }
  };

  const { RangePicker } = DatePicker;

  return (
    <>
      {contextHolder}
      <Row className="inner-header">
        <LoggedinHeader amORGUser />
      </Row>
      <Row className="content-center pt-0" data-test="manageEmp">
        <Layout>
          <Layout>
            <OrgSidebar amORGUser data={props} />
            <Content className="">
              <Row className="py-3">
                <h1>{EDIT_HOTLINE}</h1>
              </Row>
              <div className="mid-content mt-4">
                <Form
                  data-test="createHotlineStepOne"
                  className="input-md w-md-100 w-75 floating-label"
                  name="validate_other"
                  form={form}
                >
                  {error && (
                    <Form.Item>
                      <Alert
                        message={REQUIRED_MESSAGE}
                        type="error"
                        showIcon
                      ></Alert>
                    </Form.Item>
                  )}

                  <FloatLabel label={DIRECTORY_NAME} value={hotLineData.name}>
                    <Form.Item
                      className="required"
                      name="name"
                      id="name"
                      rules={[
                        {
                          required: true,
                          message: { ENTER_DIRECTORY_NAME },
                        },
                      ]}
                    >
                      <TextArea
                        showCount
                        maxLength={80}
                        value={hotLineData.name}
                        onChange={(e) => handleStepOneOnChange(e)}
                        onBlur={(e) => CheckHotlineName(e)}
                        id="name"
                        name="name"
                        className="input-control textarea-input"
                        htmlFor="name"
                        autoComplete="off"
                      />
                      {hotlineErrorUpdated == "status: false" &&
                        hotLineData.name.replace(/^\s*/, "").replace(/\s*$/, "")
                          .length > 0 &&
                        hotLineData.name.length > 0 && (
                          <CheckCircleTwoTone
                            className="successCheck"
                            twoToneColor="#52c41a"
                          />
                        )}
                      {hotlineErrorUpdated == "status: true" && (
                        <Alert
                          message={HOTLINE_ALREADY_EXISTS}
                          type="error"
                          showIcon
                        ></Alert>
                      )}
                    </Form.Item>
                  </FloatLabel>

                  <FloatLabel label={DESCRIPTION} value={hotLineData.help_text}>
                    <Form.Item
                      className="required"
                      name="help_text"
                      id="help_text"
                      rules={[
                        {
                          required: true,
                          message: ENTER_DESCRIPTION,
                        },
                      ]}
                    >
                      <>
                        <TextArea
                          className="input-control textarea-input"
                          value={hotLineData.help_text}
                          onChange={(e) => handleStepOneOnChange(e)}
                          id="help_text"
                          name="help_text"
                          htmlFor="help_text"
                          autoComplete="off"
                        />
                      </>
                    </Form.Item>
                  </FloatLabel>
                  <FloatLabel label={DATE} value="start_date">
                    <Form.Item className="input-control datepicker-wrapper">
                      <RangePicker
                        className="datepicker w-100 w-lg-50 mr-0 mr-lg-5 required"
                        name="start_date"
                        disabledDate={disabledDate}
                        value={hotLineData.start_date}
                        onChange={(date) =>
                          handleStepOneDatePickerChange(date, "start_date")
                        }
                        rules={[
                          {
                            required: true,
                            message: { SELECT_DATE },
                          },
                        ]}
                      />
                    </Form.Item>
                  </FloatLabel>
                  <FloatLabel label={STATUS} value={hotLineData.status_id}>
                    <Form.Item className="required" name="status">
                      <>
                        <Select
                          className="input-control w-100 w-lg-50"
                          value={hotLineData.status_id}
                          htmlFor="status"
                          onSelect={(value) =>
                            handleStepOneOnSelect(value, "status_id")
                          }
                        >
                          <Select.Option value="5">
                            {STATUS_ACTIVE}
                          </Select.Option>
                          <Select.Option value="4">
                            {STATUS_INACTIVE}
                          </Select.Option>
                        </Select>
                      </>
                    </Form.Item>
                  </FloatLabel>
                  <Form.Item className="">
                    <Form.Item
                      className="upload-control"
                      name="upload"
                      valuePropName="fileList"
                    >
                      <AddIcoModal
                        data-test="addIconComponent"
                        addIcon={addIcon}
                      />
                      {getIconText(hotLineData.hotline_icon_id.toString()) && (
                        <div className="ant-upload-list ant-upload-list-picture">
                          <div className="ant-upload-list-item ant-upload-list-item-list-type-picture">
                            <div className="ant-upload-list-item-info d-inline-flex w-100">
                              <span className="d-inline-flex align-items-center">
                                <a className="ant-upload-list-item-thumbnail">
                                  {ReactHtmlParser(iconHtml)}
                                </a>

                                <span
                                  className="ant-upload-list-item-name ant-upload-list-item-name-icon-count-1"
                                  title={getIconText(
                                    hotLineData.hotline_icon_id.toString()
                                  )}
                                >
                                  {getIconText(
                                    hotLineData.hotline_icon_id.toString()
                                  )}
                                </span>
                              </span>
                            </div>
                          </div>
                        </div>
                      )}
                    </Form.Item>
                  </Form.Item>
                  <div className="steps-action mt-5">
                    <Button
                      type="primary"
                      htmlType="submit"
                      onClick={editHotline}
                      data-test="stepOneNext"
                      style={{ margin: "0 8px" }}
                    >
                      {UPDATE}
                    </Button>

                    <Button
                      type="primary"
                      htmlType="submit"
                      onClick={cancelHotline}
                      data-test="stepOneNext"
                    >
                      {CANCEL}
                    </Button>
                  </div>
                </Form>
              </div>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </>
  );
};
export default EditHotLine;
