import React, { useEffect, useState } from "react";
import ManagePolls from "./ManagePolls";
import ShowAllPolls from "./ShowAllPolls";
import { connect } from "react-redux";
import { getPollAsync } from "../../actions/poll";

const PollsContainer = (props) => {
  const [loader, setLoader] = useState(false);
  const [statusClicked, setStatusClicked] = useState(false);

  useEffect(() => {
    const { dispatch } = props;
    dispatch(getPollAsync());
  }, [statusClicked]);

  useEffect(() => {
    if (props.pollsdata === null) {
      setLoader(true);
    } else if (props.pollsdata !== null) {
      setLoader(false);
    }
  }, [props.pollsdata]);

  const statusOnClicked = () => {
    setStatusClicked(!statusClicked);
  };

  return !loader && props.pollsdata !== null && props.pollsdata.length === 0 ? (
    <ManagePolls data={props} />
  ) : (
    <ShowAllPolls
      polls={props.pollsdata}
      data={props}
      loader={loader}
      statusOnClicked={statusOnClicked}
    />
  );
};

function mapStateToProps(state) {
  const { pollsdata } = state.poll;
  return {
    pollsdata,
  };
}

export default connect(mapStateToProps)(PollsContainer);
