import React, { Component } from "react";
import { Form, Input, Button, Checkbox, Row, Col, Alert } from "antd";
import FloatLabel from "../form/FloatLabels";
import { CommonHeader } from "../../containers";
import Amccvector from "../../assets/images/amcc-vector.svg";
import "./commonLogin.scss";
import { connect } from "react-redux";
import { login } from "../../actions/auth";
import { Redirect } from "react-router-dom";
import * as AppStorage from "../../utils/AppStorage";
import PrivacyPolicyModal from "../modal/PrivacyPolicyModal";

const layout = {
  wrapperCol: {
    span: 24,
  },
};
const tailLayout = {
  wrapperCol: {
    span: 24,
  },
};

var CryptoJS = require("crypto-js");
class ResetPassword extends Component {
  constructor(props) {
    super(props);
    this.handleLogin = this.handleLogin.bind(this);
    this.onChangeUsername = this.onChangeUsername.bind(this);
    this.onChangePassword = this.onChangePassword.bind(this);
    this.state = {
      email: "",
      password: "",
      page: "is_app_user",
      error: "false",
    };
  }
  onChangeUsername(e) {
    this.setState({
      email: e.target.value,
    });
  }
  onChangePassword(e) {
    this.setState({
      password: e.target.value,
    });
  }

  handleLogin() {
    this.setState({
      loading: true,
    });
    const { dispatch, history } = this.props;

    dispatch(login(this.state.email, this.state.password, this.state.page))
      .then(() => {
        window.location.reload();
      })
      .catch(() => {
        this.setState({
          loading: false,
          error: "true",
        });
      });
  }

  canSubmit() {
    const { email, password } = this.state;
    return email.length > 0 && password.length > 0;
  }

  render() {
    const { isLoggedIn, message } = this.props;
    let isamcc = false;
    let isorgcc = false;
    let isamweb = false;
    const isAuthenticated = AppStorage.get("user");
    if (!isAuthenticated) {
      isamcc = true;
      isorgcc = true;
      isamweb = true;
    }
    const commonForAllUsers = JSON.parse(AppStorage.get("user"));
    if (
      isAuthenticated &&
      commonForAllUsers &&
      commonForAllUsers.user_data != "null"
    ) {
      isamcc =
        CryptoJS.AES.decrypt(
          commonForAllUsers.user_data.is_amcc_user,
          "anonymoose"
        ).toString(CryptoJS.enc.Utf8) === "true";
      isorgcc =
        CryptoJS.AES.decrypt(
          commonForAllUsers.user_data.is_org_cc_user,
          "anonymoose"
        ).toString(CryptoJS.enc.Utf8) === "true";
      isamweb =
        CryptoJS.AES.decrypt(
          commonForAllUsers.user_data.is_am_web_user,
          "anonymoose"
        ).toString(CryptoJS.enc.Utf8) === "true";
    }

    const isEnabled = this.canSubmit();
    if (isLoggedIn) {
      if (isorgcc) {
        return <Redirect to="/orgcc/manageemployees" />;
      } else if (isamweb) {
        return <Redirect to="/amweb/hotlinemessages" />;
      } else if (isamcc) {
        return <Redirect to="/amcc/manageorganizations" />;
      }
    }

    return (
      <>
        <Row className="px-4 py-1 py-lg-3 header">
          <CommonHeader />
        </Row>
        <Row className="content-center mt-0 px-4">
          <Col
            sm={24}
            lg={12}
            className="vector-area justify-content-center mt-1"
          >
            <Col span={24} className="text-center">
              <h5 className="text-center primary-text">
                Control Center and Insights Dashboard
              </h5>
            </Col>
            <Row className="justify-content-center mt-3">
              <img
                src={Amccvector}
                alt="Anonymoose"
                className="pr-0 pr-lg-4"
              />
            </Row>
            <Row className="pt-3">
              <p className="w-100 text-center text-wgt-md mb-1 d-none d-lg-block">
                Raising Voice Anonymously Made Easy
              </p>
            </Row>
          </Col>
          <Col
            lg={{ span: 12 }}
            md={{ span: 24 }}
            xs={{ span: 24 }}
            className="d-flex justify-content-center mt-2"
          >
            <div className=" col-md-24 col-lg-12 col-xl-10 login-box justify-content-center d-flex">
              {this.state.error == "true" && (
                <Alert
                  message="Invalid Login! Please try again"
                  type="error"
                  showIcon
                ></Alert>
              )}
              <Row className="mb-4 d-none d-lg-block">
                <h2>Reset Password</h2>
                <p className="mt-3 font-weight-bold">
                  Set a new password for your account.
                </p>
              </Row>

              <Form
                {...layout}
                onSubmit={this.handleLogin}
                ref={(c) => {
                  this.form = c;
                }}
                name="basic"
                initialValues={{
                  remember: true,
                }}
              >
                <FloatLabel label="Password">
                  <Form.Item
                    name="password"
                    rules={[
                      {
                        required: true,
                        message: "Please enter your password!",
                      },
                    ]}
                  >
                    <Input
                      type="password"
                      className="input-control"
                      value={this.state.password}
                      onChange={this.onChangePassword}
                    />
                  </Form.Item>
                </FloatLabel>

                <FloatLabel label="Confirm Password">
                  <Form.Item
                    name="confirmpassword"
                    rules={[
                      {
                        required: true,
                        message: "Please confirm your password!",
                      },
                    ]}
                  >
                    <Input
                      type="password"
                      className="input-control"
                      value={this.state.password}
                      onChange={this.onChangePassword}
                    />
                  </Form.Item>
                </FloatLabel>

                <Form.Item {...tailLayout}>
                  <Button
                    type="primary"
                    htmlType="submit"
                    className="primary-btn-lg w-100 mt-4"
                  >
                    RESET PASSWORD
                  </Button>
                </Form.Item>
              </Form>
            </div>
          </Col>
        </Row>
      </>
    );
  }
}

export default ResetPassword;
