import React from "react";
import { Layout, Row, Col, Input, Select } from "antd";
import { LoggedinHeader } from "../../containers";
import Grid from "../table/Table";
import Buttons from "../button/Button";
import { connect } from "react-redux";
import { getManageEmpAsync } from "../../actions/manageEmp";
import { AudioOutlined } from "@ant-design/icons";
import GridPopover from "../table/Popover";
import OrgSidebar from "../../containers/OrgSidebar";
import { ADD_EMPLOYEE, MANAGE_EMPLOYEES, SEARCH } from "../../../src/headings";
import {
  ACCESS,
  FIRST_NAME,
  LAST_NAME,
  MOBILE,
  STATUS,
  ACTIONS,
} from "../../labels";
import { STATUS_ACTIVE } from "../../values";

const { Search } = Input;

const suffix = (
  <AudioOutlined
    style={{
      fontSize: 16,
      color: "#1890ff",
    }}
  />
);

const { Option } = Select;
const { Content } = Layout;

class ManageEmp extends React.Component {
  constructor(props) {
    super(props);
    const columns = [
      {
        title: FIRST_NAME,
        dataIndex: "firstname",
        key: "firstname",
        width: "22%",
        render: (firstname) => {
          return {
            children: firstname,
            props: {
              "data-title": "First Name",
            },
          };
        },
      },
      {
        title: LAST_NAME,
        dataIndex: "lastname",
        key: "lastname",
        width: "22%",
        render: (lastname) => {
          return {
            children: lastname,
            props: {
              "data-title": "Last Name",
            },
          };
        },
      },
      {
        title: MOBILE,
        dataIndex: "Mobile_Number",
        key: "Mobile_Number",
        width: "20%",
        render: (mobNumber) => {
          return {
            children: mobNumber,
            props: {
              "data-title": "Mobile Number",
            },
          };
        },
      },
      {
        title: ACCESS,
        dataIndex: "Access",
        key: "Access",
        render: (res) => {
          let test = "";
          let flag = false;
          for (let property in res) {
            if (res[property] == true) {
              if (property == "am_web") {
                property = "AM WEB";
                flag = true;
              }
              if (property == "org_cc_user") {
                property = "Org CC";
                flag = true;
              }
              if (property == "app_user") {
                if (!flag) {
                  property = "APP";
                }
              }

              if (test.length == 0) {
                test += property;
              } else if (property !== "app_user") {
                if (test.includes("APP")) {
                  test = property;
                } else {
                  test += ", " + property;
                }
              }
            }
          }

          return {
            children: test,
            props: {
              "data-title": "Access",
            },
          };
        },
        width: "20%",
      },
      {
        title: STATUS,
        dataIndex: "status",
        key: "status",
        width: "10%",
        render: () => {
          return {
            children: (
              <div className="primary-tag-rounded tag">
                <span className="circle mr-1"></span>
                {STATUS_ACTIVE}
              </div>
            ),
            props: {
              "data-title": "Status",
            },
          };
        },
      },
      {
        title: ACTIONS,
        dataIndex: "Actions",
        key: "x",
        width: "10%",
        render: () => {
          return {
            children: <GridPopover />,
            props: {
              "data-title": "Actions",
            },
          };
        },
      },
    ];
    this.state = {
      columns: columns,
      data: [],
    };
  }
  componentDidMount() {
    const { dispatch } = this.props;
    dispatch(getManageEmpAsync());
  }
  UNSAFE_componentWillReceiveProps(props) {
    let { manageEmpData } = props;
    this.setState({ data: manageEmpData }, () => {
      this.onSearch("");
    });
  }
  filterIt(arr, searchKey) {
    if (!searchKey || searchKey === "" || searchKey === null) {
      return arr;
    }
    return arr.filter(function (obj) {
      return Object.keys(obj).some(function (key) {
        if (typeof obj[key] === "string") {
          return obj[key].toUpperCase().includes(searchKey.toUpperCase());
        }
      });
    });
  }

  onSearch = (value) => {
    const { data } = this.state;
    const filterDt = this.filterIt(data, value);
    this.setState({ filterData: filterDt });
  };
  render() {
    return (
      <>
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0" data-test="manageEmp">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser data={this.props} />
              <Content className="">
                <Row className="py-3">
                  <h1>{MANAGE_EMPLOYEES}</h1>
                </Row>
                <Col span={24}>
                  <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
                    <Col span={24} lg={18} className="search-row mt-2 mt-lg-0">
                      <Row>
                        <Col span={24} lg={9} className="search-box">
                          <Search
                            placeholder={SEARCH}
                            onSearch={this.onSearch}
                          />
                        </Col>
                        <Col
                          span={24}
                          lg={9}
                          className="ml-0 ml-lg-4 mt-2 mt-lg-0"
                        >
                          <Select className="w-100 w-md-75" disabled>
                            <Option value="1" defaultValue>
                              1
                            </Option>
                            <Option value="2">2</Option>
                          </Select>
                        </Col>
                      </Row>
                    </Col>

                    <Col
                      span={24}
                      lg={6}
                      className="d-flex align-items-center justify-content-end"
                    >
                      <Buttons value={ADD_EMPLOYEE} />
                    </Col>
                  </Row>
                </Col>
                <Grid
                  rowKey="unique_identifier"
                  columns={this.state.columns}
                  data={this.state.filterData}
                />
              </Content>
            </Layout>
          </Layout>
        </Row>
      </>
    );
  }
}
function mapStateToProps(state) {
  const { manageEmpData } = state.manageEmp;
  const { message } = state.message;
  return {
    manageEmpData,
    message,
  };
}

export default connect(mapStateToProps)(ManageEmp);
