import React, { useState } from "react";
import {
  Layout,
  Row,
  Col,
  Breadcrumb,
  Button,
  Form,
  Input,
  DatePicker,
  Select,
} from "antd";
import { NavLink } from "react-router-dom";
import { LoggedinHeader } from "../../containers";
const { Content } = Layout;

import "react-phone-input-2/lib/style.css";
import styles from "./manageEmp.module.scss";
import "../form/form.scss";
import FloatLabel from "../form/FloatLabels";
import PhoneInput from "react-phone-input-2";

const { RangePicker } = DatePicker;
const CreateManually = () => {
  return (
    <React.Fragment>
      <React.Fragment>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <Content className={`w-100 ml-0 ${styles.no_sidebar}`}>
              <Row className="pt-3">
                  <Breadcrumb separator=">">
                    <NavLink to="/addemployee">
                      <Breadcrumb.Item>Add Employee</Breadcrumb.Item>
                    </NavLink>
                    <Breadcrumb.Item>Create Manually</Breadcrumb.Item>
                  </Breadcrumb>
                </Row>
                <Row className="py-3">
                  <h1 className="mb-0">Create Manually</h1>
                </Row>
                <div className="mid-content mt-4">
                  <Row className="d-flex justify-content-center align-items-center">
                  <Form className="input-md floating-label w-100" name="">
                      <Col span={24}>
                        <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="First Name">
                              <Form.Item
                                className="required"
                                name="fname"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please enter your first name",
                                  },
                                ]}
                              >
                                <Input
                                  className="input-control"
                                  id="fname"
                                  htmlFor="fname"
                                />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="Last Name">
                              <Form.Item
                                className="required"
                                name="lname"
                                validateStatus="success"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please enter your last name",
                                  },
                                ]}
                              >
                                <Input
                                  className="input-control"
                                  id="lname"
                                  htmlFor="lname"
                                />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="Email id">
                              <Form.Item
                                className="required"
                                name="emailid"
                                validateStatus="success"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please enter your email id",
                                  },
                                ]}
                              >
                                <Input
                                  type="email"
                                  className="input-control"
                                  id="emailid"
                                  htmlFor="emailid"
                                />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row phone-field">
                            <FloatLabel label="Mobile Number">
                              <Form.Item
                                className="required"
                                name="mobile"
                                validateStatus="success"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please enter your mobile number",
                                  },
                                ]}
                              >
                                <PhoneInput country={"us"} />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="Employee id">
                              <Form.Item
                                className="required"
                                name="empid"
                                validateStatus="success"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please enter your employee id",
                                  },
                                ]}
                              >
                                <Input
                                  className="input-control"
                                  id="empid"
                                  htmlFor="empid"
                                />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="Department">
                              <Form.Item
                                className="required"
                                name="department"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please enter your department",
                                  },
                                ]}
                              >
                                <Select className="input-control w-100">
                                  <Option value="1">dept1</Option>
                                  <Option value="0">dept2</Option>
                                </Select>
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="Status">
                              <Form.Item
                                className="required"
                                name="status"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please enter status",
                                  },
                                ]}
                              >
                                <Select className="input-control w-100">
                                  <Option value="1">1</Option>
                                  <Option value="0">2</Option>
                                </Select>
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="Select Date">
                              <Form.Item
                                className="input-control datepicker-wrapper required"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please select start and end date",
                                  },
                                ]}
                              >
                                <RangePicker className="w-100 " name="s1Date" />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="Make Super User">
                              <Form.Item
                                className="required"
                                name="status"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please select user type",
                                  },
                                ]}
                              >
                                <Select className="input-control w-100">
                                  <Option value="1">Make Super User</Option>
                                  <Option value="0">Make User</Option>
                                </Select>
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="F1">
                              <Form.Item name="f1">
                                <Input className="input-control" htmlFor="f1" />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="F2">
                              <Form.Item name="f2">
                                <Input className="input-control" htmlFor="f3" />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="F3">
                              <Form.Item name="f3">
                                <Input className="input-control" htmlFor="f3" />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="F4">
                              <Form.Item name="f4">
                                <Input className="input-control" htmlFor="f4" />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="F5">
                              <Form.Item name="f5">
                                <Input className="input-control" htmlFor="f5" />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                        </Row>
                      </Col>

                      <Form.Item>
                        <Button type="primary" htmlType="submit">
                          Save
                        </Button>
                      </Form.Item>
                    </Form>
                  </Row>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};

export default CreateManually;
