import React, { Component } from "react";
import { Modal, Form, Col, DatePicker } from "antd";
import FloatLabel from "../form/FloatLabels";
import moment from "moment";
import "../modal/modal.scss";
import Grid from "@material-ui/core/Grid";
import "date-fns";
import DateFnsUtils from "@date-io/date-fns";
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
} from "@material-ui/pickers";
import {
  CANCEL,
  EXTEND_END_DATE,
  EXTEND_TEXT_MODAL,
  POLL,
} from "../../headings";
import {
  EXTEND_END_DATE_TEXT_PART1,
  EXTEND_END_DATE_TEXT_PART2,
} from "../../messages";
import { SELECT_END_DATE, SELECT_END_TIME } from "../../labels";
import { SELECT_DATE } from "../../errors";

let startDate = "";

class ExtendEndDate extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      pollEndDate: "",
      pollEndTime: new Date(),
    };
    this.range = this.range.bind(this);
    this.disabledDateTime = this.disabledDateTime.bind(this);
  }

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  // componentDidMount() {
  //   console.log(
  //    '***********'
  //   );
  //   // const s_date = moment(
  //   //   this.props.pollsData.start_date + " " + this.props.pollsData.start_time
  //   // );
  //   // const sDate = moment(s_date)
  //   //   .utcOffset("+05:30")
  //   //   .format("YYYY/MM/DD HH:mm:ss");
  //   // const eDate = moment(
  //   //   this.props.pollsData.end_date + " " + this.props.pollsData.end_time
  //   // )
  //   //   .utcOffset("+05:30")
  //   //   .format("YYYY/MM/DD HH:mm:ss");
  //   // this.setState({ pollStartDate: moment(this.props.pollsData.start_date),
  //   //   pollEndDate: this.props.pollsData.end_date,
  //   //   pollStartTime: this.props.pollsData.start_time,
  //   //   pollEndTime: this.props.pollsData.end_time,
  //   //  });
  // }

  handleOk = () => {
    this.setState({
      visible: false,
    });
    let obj = {
      poll_id: this.props.pollsData.poll_id,
      pollName: this.props.pollsData.title,
    };
    this.props.extendPollDate(obj, this.state);
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
    window.location = "/orgcc/pollsdirectory";
  };

  onChangeEndDate(date) {
    this.setState({
      pollEndDate: date,
    });
  }

  onChangeEndTime(time) {
    this.setState({
      pollEndTime: time,
    });
  }

  disabledDate(current) {
    return current && current < moment().startOf("day");
  }

  range(start, end) {
    const result = [];
    for (let i = start; i < end; i++) {
      result.push(i);
    }
    return result;
  }

  disabledDateTime(current, type) {
    if (type === "start") {
      startDate = current;
      if (moment(current).isSame(moment(), "day")) {
        var now = moment();
        return {
          disabledHours: () => this.range(0, now.hour()),
          disabledMinutes: () => this.range(0, now.minutes()),
          disabledSeconds: () => this.range(0, now.seconds()),
        };
      }
    } else if (type === "end") {
      if (moment(current).isSame(moment(), "day")) {
        var now = moment();
        return {
          disabledHours: () => this.range(0, now.hour()),
          disabledMinutes: () => this.range(0, now.minutes()),
          disabledSeconds: () => this.range(0, now.seconds()),
        };
      } else if (moment(current).isSame(moment(startDate))) {
        var now = moment(startDate);
        return {
          disabledHours: () => this.range(0, now.hour()),
          disabledMinutes: () => this.range(0, now.minutes()),
          disabledSeconds: () => this.range(0, now.seconds()),
        };
      }
    }
  }

  render() {
    return (
      <>
        <a onClick={this.showModal}>{EXTEND_END_DATE}</a>
        <Modal
          title={EXTEND_END_DATE}
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={520}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            className: "ant-btn-secondary",
            children: "Custom cancel",
          }}
          okText={EXTEND_TEXT_MODAL}
          cancelText={CANCEL}
        >
          <Form className="input-md floating-label w-100">
            <Col span={24} className="gutter-row">
              <div className="mb-4 subtext-md font-weight-md6">
                {EXTEND_END_DATE_TEXT_PART1}{" "}
                <span className="pr-1"> {EXTEND_END_DATE_TEXT_PART2}</span>
                {this.props.pollsData.title} {POLL}
              </div>

              <FloatLabel label={SELECT_END_DATE} value="eDate">
                <Form.Item
                  name="eDate"
                  rules={[
                    {
                      required: true,
                      message: SELECT_DATE,
                    },
                  ]}
                >
                  <DatePicker
                    value={this.state.pollEndDate}
                    disabledDate={this.disabledDate}
                    onChange={(date) => this.onChangeEndDate(date)}
                    className="w-100"
                    name="eDate"
                    className="required"
                  />
                  <MuiPickersUtilsProvider utils={DateFnsUtils}>
                    <Grid container justify="space-around">
                      <KeyboardTimePicker
                        margin="normal"
                        id="time-picker2"
                        label={SELECT_END_TIME}
                        value={this.state.pollEndTime}
                        onChange={(time) => this.onChangeEndTime(time)}
                        KeyboardButtonProps={{
                          "aria-label": "change time",
                        }}
                      />
                    </Grid>
                  </MuiPickersUtilsProvider>
                </Form.Item>
              </FloatLabel>
            </Col>
          </Form>
        </Modal>
      </>
    );
  }
}

export default ExtendEndDate;
