import React, { useState } from "react";
import { Layout, Row, Col, Breadcrumb, Button } from "antd";
import { NavLink } from "react-router-dom";
import { LoggedinHeader } from "../../containers";
const { Content } = Layout;
import styles from "./manageEmp.module.scss";
import UploadFiles from "./UploadFiles";

const ImportFiles = () => {
  return (
    <React.Fragment>
      <React.Fragment>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <Content className={`w-100 ml-0 ${styles.no_sidebar}`}>
                <Row className="pt-3">
                  <Breadcrumb separator=">">
                    <NavLink to="/addemployee">
                      <Breadcrumb.Item>Add Employee</Breadcrumb.Item>
                    </NavLink>
                    <Breadcrumb.Item>Import CSV file</Breadcrumb.Item>
                  </Breadcrumb>
                </Row>
                <Row className="py-3">
                  <h1 className="mb-0">Import CSV file </h1>
                </Row>
                <div className="mid-content mt-4">
                  <Row className="d-flex justify-content-center align-items-center">
                    <Col span={20} className="mt-3 dragdrop">
                      <UploadFiles />
                    </Col>
                    <Col
                      span={24}
                      className="justify-content-center d-flex mt-5"
                    >
                      <Button type="link" className="link pl-0">
                        <span className="icon icon-download-icon" />
                        Download Sample File
                      </Button>
                    </Col>
                  </Row>
                  <div className="mt-3 btn-row">
                    <Button
                      type="secondary"
                      className="mr-4 mb-4"
                      type="secondary"
                    >
                      Cancel
                    </Button>
                    <Button type="primary" className="md">
                      Upload
                    </Button>
                  </div>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};

export default ImportFiles;
